import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager.module.css";
import FinanceDaybookReceiptCard from "./FinanceDaybookReceiptCard";
import { useFinanceDayBookReceiptList } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";

const FinanceDaybookReceipt = ({ fid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { financeDayBookReceiptList, financeDayBookReceiptListLoading } =
    useFinanceDayBookReceiptList({
      data: {
        fid: fid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !fid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeDayBookReceiptList?.list?.length) {
      let obj = nextPage(financeDayBookReceiptList?.list?.length);
      setState(() => obj);
    }
  }, [financeDayBookReceiptList?.list]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>

      {financeDayBookReceiptList?.list?.map((repay, index) =>
        financeDayBookReceiptList?.list?.length === index + 1 ? (
          <div key={repay?._id} ref={ref}>
            <FinanceDaybookReceiptCard repay={repay} />
          </div>
        ) : (
          <FinanceDaybookReceiptCard repay={repay} key={repay?._id} />
        )
      )}
      {financeDayBookReceiptListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        financeDayBookReceiptList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No receipts found related this search."
          />
        )
      ) : (
        financeDayBookReceiptList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No receipts added."
          />
        )
      )}
    </>
  );
};

export default FinanceDaybookReceipt;
