import { useLocation, useParams, useSearchParams } from "react-router-dom";
import FinancePaymentDaybook from "./FinancePaymentDaybook";
import FinanceDaybookReceipt from "./FinanceDaybookReceipt";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import BorderBottom from "../../../../Ui/Border/BorderBottom";

const tabList = [
  {
    label: "receipts",
    id: "receipt",
    urlKey: "",
  },
  {
    label: "payments",
    id: "payment",
    urlKey: "&t=payment",
  },
];

const FinanceDaybookConfig = ({ fid }) => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  return (
    <>
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={
              tab.id === "receipt" && !searchParams.get("t")
                ? true
                : searchParams.get("t") === tab.id
                ? true
                : false
            }
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/finance?type=history&n=day-book${tab.urlKey}`
                : `/q/${params.username}/member/staff/finance?type=history&n=day-book${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      {!searchParams.get("t") ? (
        <FinanceDaybookReceipt fid={fid} />
      ) : searchParams.get("t") === "payment" ? (
        <FinancePaymentDaybook fid={fid} />
      ) : null}
    </>
  );
};

export default FinanceDaybookConfig;
